          },
        }
      ],
      currentImage: {},
      questionHistory: [],
      currentIndex: 0,
      answer: '',
      showHint: false,
    }
  },
  methods: {
    onAnswer() {
      if (this.currentImage.labels[this.currentLocale].indexOf(this.answer) > -1) {
        !this.currentQuestionIsInHistory() ? this.addCurrentQuestionToHistory() : this.removeCurrentQuestionFromHistory();
        this.setNewQuestion();
      }
    },
    currentQuestionIsInHistory() {
      return this.questionHistory.indexOf(this.currentIndex) > -1;
    },
    addCurrentQuestionToHistory() {
      this.questionHistory.push(this.currentIndex);
    },
    removeCurrentQuestionFromHistory() {
      this.questionHistory = this.questionHistory.filter(e => e !== this.currentIndex);
    },
    setNewQuestion() {
        this.answer = '';
        var newImageIndex = this.getNewRandomQuestionIndex();
        this.currentImage = this.images[newImageIndex];
        this.currentIndex = newImageIndex;      
    },
    getNewRandomQuestionIndex() {
      var res = Math.floor(Math.random() * this.images.length);
      while (res === this.currentIndex || this.questionHistory.indexOf(res) > -1) {
        res = Math.floor(Math.random() * this.images.length);
      }
      return res;
    },
    toggleHint(val) {
      this.showHint = val !== null ? val : !this.showHint;
    },
  },
  mounted() {
    this.setNewQuestion();
    window.addEventListener('keydown', (e) => {
      if (e.key == 'Control') {
        this.toggleHint(true);
      }
    });
    window.addEventListener('keyup', (e) => {
      if (e.key == 'Control') {
        this.toggleHint(false);
      }
    });
    this.setNewQuestion();
  },
  props: {
    msg: String
  }
}
</script>

